import React from 'react';
import { NavLink } from 'react-router-dom';

const Navigation = () => {
    return (
        <div className="Navigation">
            <ul>
				<li>
                    <NavLink exact to="/" activeClassName="navActive">
                        <span>Accueil</span>
                    </NavLink>
                </li>
				<li>
                    <NavLink exact to="/etudes" activeClassName="navActive">
                        <span>Etudes</span>
                    </NavLink>
                </li>
				<li>
                    <NavLink exact to="/loisirs" activeClassName="navActive">
                        <span>Loisirs</span>
                    </NavLink>
                </li>
				<li>
                    <NavLink exact to="/contact" activeClassName="navActive">
                        <span>Contact</span>
                    </NavLink>
                </li>
            </ul>
        </div>
    );
};

export default Navigation;