import React from 'react';
import StudyText from './pages/StudyText';
import ContactText from './pages/ContactText';
import HobbyText from './pages/HobbyText';
import { useLocation } from 'react-router-dom'


const PageFrame = () => {
    let location = useLocation();

    let component;

    if (location.pathname === "/etudes") {
        component = <StudyText/>;
    }
    else if (location.pathname === "/loisirs"){
        component = <HobbyText/>;
    }
    else if (location.pathname === "/contact"){
        component = <ContactText/>;
    }
    

    return (
        <div className="PageFrame">
            <div className="frame">
                {component}
            </div>
        </div>
    );
};

export default PageFrame;