//navigation du site
//rsc
import React from 'react';

import { BrowserRouter, Route, Switch } from 'react-router-dom'; //si on met pas le chemin, il va chercher une biblio dans node_modules

import Home from './pages/Home.js';
import Study from './pages/Study.js';
import Hobby from './pages/Hobby.js';
import Contact from './pages/Contact.js';
import NotFound from './pages/NotFound.js';

const App = () => {
    return (
        <BrowserRouter>
            <Switch>
                <Route path="/" exact component={Home} />
                <Route path="/etudes" component={Study} />
                <Route path="/loisirs" component={Hobby} />
                <Route path="/contact" component={Contact} />
                <Route component={NotFound} />
            </Switch>
        </BrowserRouter>
    );
};

export default App;
