import React from 'react';
import {FaInstagram} from "react-icons/fa";

const HobbyText = () => {
    return (
        <div className="HobbyText">
            <div id="sport">
                <h3>SPORT</h3>
            </div>
            <div id="judo">
                <h3>Judo - Jujitsu</h3>
                <h3>Ceinture Marron</h3>
            </div>
            <div id="art">
                <h3>ART</h3>
            </div>
            <div id="dessin">
                <h3>Dessin</h3>
                <div className="insta">
                    <a href="https://www.instagram.com/bl_tistou/" target="_blank" rel="noopener noreferrer">
                        <FaInstagram />
                    </a>
                </div>
            </div>
        </div>
    );
};

export default HobbyText;